import React, { Suspense } from "react";

import { Container, Row, Col } from "react-bootstrap";

// import ProdukCard from "./ProdukCard";

const ProdukCard = React.lazy(() => import("./ProdukCard"));

function ProdukPage() {
  return (
    <Suspense fallback={<div className="container mx-auto">Loading...</div>}>
      <Container>
        <h1 className="font-weight-700 text-landing-title">Produk Kami</h1>
        <Row>
          <Col xs={12} sm={3}>
            <ProdukCard
              title="Cold White"
              description="Cold Brew + Susu segar + Gula"
            />
          </Col>
          <Col xs={12} sm={3}>
            <ProdukCard title="Cold Black" description="Cold Brew + Gula" />
          </Col>
          <Col xs={12} sm={3}>
            <ProdukCard title="Thai Tea" description="Thai Tea + Krimer" />
          </Col>
          <Col xs={12} sm={3}>
            <ProdukCard title="Coklat" description="Bubuk Coklat + Krimer" />
          </Col>
        </Row>
      </Container>
    </Suspense>
  );
}
export default ProdukPage;
